import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
import uniSearchBar from "@/components/uni-search-bar/uni-search-bar.vue"
export default {
	components: {
		uniDrawer,
		uniSearchBar
	},
	data() {
		return {
			dataList: [],
			keyWords: '',
			childList: []
		}
	},
	onLoad() {
		this.loadData();
	},
	watch: {
		keyWords(newVal, oldVal) {
			if (oldVal != '') {
				this.clear()
			}
		}
	},
	methods: {
		clear() {
			this.dataList = [];
			this.loadData();
		},
		hideTwo() {
			this.$refs.drawer.close()
		},
		loadData() {
			let parmas = {};
			if (this.keyWords != '') {
				parmas.keyWords = this.keyWords;
			}
			this._reqw
				.ipost(parmas, 'jobClassification')
				.then(res => {
					res.result == 0 ? this.dataList = res.dataList : this.$api.tip(res.resultNote);
				})
				.catch(err => {});

		},
		searchConfirm(e) {
			console.log(e)
			this.keyWords = e.value;
			this.clear();
		},
		onClickItem(id) {
			let parmas = {
				id: id
			};
			console.log(parmas, 'CategoryListTwo')
			this._reqw
				.ipost(parmas, "CategoryListTwo")
				.then(res => {
					res.result == 0 ? (this.childList = res.dataList, this.$refs.drawer.open()) : this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		},
		choseSkilled(e) {
			let arry = this.$api.prePage().expectedWork;
			console.log(arry)
			this.$set(arry, arry.length, e);
			this.$api.prePage().expectedWork = arry;
			this.$refs.drawer.close();
			setTimeout(() => {
				this.$api.back()
			}, 300)
		}
	}
}
